import React, { useEffect } from 'react'
import { Layout } from '../components/Layout'
import { Badge, Code, Container, Heading } from '@chakra-ui/react'
import './Profilepage.css'
import {AiOutlinePlus} from 'react-icons/ai'
import {useState} from 'react'
import Todo from './Todo'
import { Card } from '../components/Card'
import {db} from '../utils/init-firebase'
import{query,collection, onSnapshot, updateDoc,doc, addDoc,deleteDoc} from 'firebase/firestore'
import { useAuth } from '../contexts/AuthContext'
export default function Profilepage() {
 
  useEffect(()=>{
    const q = query(collection(db,currentUser.uid))
    const unsubscribe = onSnapshot(q,(querySnapshot) => {
      let todosArr = []
      querySnapshot.forEach((doc) => {
        todosArr.push({...doc.data(), id:doc.id})
      });
      setTodos(todosArr)
    })
    return() => unsubscribe()
  },[])
  
  const {currentUser} = useAuth()
  //const todos = currentUser.uid;
  const [input,setInput] = useState('');
  const createTodo = async(e) =>{
    e.preventDefault(e)
    if(input===''){
      alert('invalid')
      return
    }

    await addDoc(collection(db,currentUser.uid),{
    text:input,
    completed:false,
    })
    setInput('')
  }
console.log(input)
console.log(currentUser)
  const [todos,setTodos] = useState([]);
  const toggleComplete = async (todo) => {
    await updateDoc(doc(db,currentUser.uid, todo.id) ,{
      completed: !todo.completed
    })
  }


  const deleteTodo = async (id) =>{
     await deleteDoc(doc(db,currentUser.uid,id))
  }

  return (
    <Layout>
      <Heading className='mid'>
        TO DO List
      </Heading>
      <Card maxW='md' mx='auto' mt={4}>
      <div className="container">
        <h3 className="heading">TO DO LIST</h3>
        <form onSubmit={createTodo} className="form">
          <input value={input} onChange={(e)=>setInput(e.target.value)} className="inni" type='text' placeholder='Add'></input>

          <button className="button"><AiOutlinePlus size={30}/></button>

        </form>
        <ul className="ooo">
          {todos.map((todo,index)=>(
            <Todo key={index} todo={todo} toggleComplete={toggleComplete} deleteTodo={deleteTodo}/>
            ))}
        </ul>
        
      </div>

          </Card>
      <Container maxW='container.lg' overflowX='auto' py={4}></Container>
    </Layout>
  )
}
